import React from 'react';
import ReactDOM from 'react-dom/client';
import io from "socket.io-client";
import ReactHtmlParser from 'react-html-parser';

const socket = io.connect("/",{
  path: "/api/socket/"
});

const msgInput = React.createRef()
class Messages extends React.Component {
    constructor(props){
        super(props);
        this.currentdate = new Date();
        this.Time = Math.floor(this.currentdate.getTime()/1000);
        this.state = {messages:[],time:this.Time}
        socket.on("getAlert",(data)=>{
            this.handleAlert(data)
        })
        socket.on("getMessage",(data)=>{
             this.handle(data)
        })
        socket.on("alert",(data)=>{
            alert(data);
            window.location.href = "/";
        })
    }
    componentDidMount(){
        fetch('/global/latest').then(response => response.json()).then(data=>this.setState({messages:data}))
        
    }
    componentDidUpdate(){
        var elem = document.getElementById('msgbox');
        elem.scrollTop = elem.scrollHeight;

    }
    handle = (data) => {
        this.currentdate = new Date();
        this.Time = Math.floor(this.currentdate.getTime()/1000);
        this.setState({messages : this.state.messages.concat(data),time:this.Time})
    }
    handleAlert = (data) => {
        console.log(123)
        var systemname = <span class='admin'>PESify</span>
        data.alert = <span class='alert'><b>{data.name + " joined with ID " + data.id} </b></span>
        let alertobj = {'uname':systemname,'id':" Alert","time":data.time,"msg":data.alert}
        this.currentdate = new Date();
        this.Time = Math.floor(this.currentdate.getTime()/1000);
        this.setState({messages : this.state.messages.concat(alertobj),time:this.Time})
    }
    dateparser = (data) => { 
        var t = new Date(data)
        t = Math.floor(t.getTime()/1000);
        let val = this.state.time - t;
        if (val<0) {
            return 0 + " seconds ago"
        }
        if(val<60) {
            return val + " seconds ago"
        }
        if (val>=60 && val <3600) {
            return Math.floor(val/60) + " minutes ago"
        }
        if (val>=3600 && val <86400) {
            return Math.floor(val/3600) + " hours ago"
        }
        if (val>=86400) {
            return Math.floor(val/86400) + " days ago"
        }

    }
    render(){
        let obj = this.state.messages.map((data) => (
            <div>
                <table class="message">
                    <tr>
                        <td class="info">
                            <span class="uname"><b>{data.uname}</b></span>
                            <span class="id">ID:{data.id}</span>
                            <span class="time"> {this.dateparser(data.time)}</span>
                        </td>
                    </tr>
                    <tr>
                        <td class="msg">
                            <span>{data.msg}</span>
                        </td>
                    </tr>
                </table>
            </div>
            ))
        return obj
    }
}

const send = () => {
    let currentTime = new Date();
    let msg = {message:msgInput.current.value,time:currentTime};
    console.log(msg)
    socket.emit("send-message",msg)
    msgInput.current.value="";
}

const inputHandler = (event) => {
    if(event.keyCode==13){
        send();
        
    }
    
}
const buttonHandler = (event) => {
    send();
}



const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(<div id="container">
            
            <div style={{width:"80%",margin:"0 auto"}}>
              <div id="msgbox">
              <Messages/>
              </div>
              <div style={{width:"100%",height:"40px",marginTop: "20px"}}>
              <input type="text" id="imsg" onKeyUp={inputHandler} ref={msgInput}/><button onClick={buttonHandler} style={{borderTopRightRadius: "40px",borderBottomRightRadius: "40px"}}><b>Send</b></button>
              </div>
            </div>
            </div>);